* {
    box-sizing: border-box;
  }
  
  html,
  body {
    margin: 0;
    padding: 0;
    overflow: visible !important;
    overflow-x: hidden;
  }
  
  .before,
  .after {
    height: 50vh;
    background: lightgrey;
    padding: 1em;
  }
  
  .scroll {
    position: relative;
    top: 0;
    width: 100%;
    height: auto;
  }
  
  .scroll__view {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    top: 0;
  }
  
  .scroll__overlays,
  .scroll__hero {
    flex: 1 0 50%;
  }
  @media screen and (max-width: 768px) {
    .scroll__overlays,
  .scroll__hero {
      flex: 1 0 100%;
    }
  }
  
  .scroll__overlays {
    position: relative;
    z-index: 3;
  }
  
  .scroll__content {
    overflow: hidden;
    width: 100%;
    height: 100vh;
    top: 0;
    position: relative;
  }
  @media screen and (min-width: 768px) {
    .scroll__content {
      position: sticky;
    }
  }
  .scroll__content .scroll__text {
    background: white;
    padding: 2em;
    word-break: break-word;
  }
  .scroll__content .scroll__text:before {
    content: "";
    width: 100%;
    height: 50vh;
    background: red;
    background: linear-gradient(rgba(255, 255, 255, 0), white);
    position: absolute;
    top: 0;
    transform: translateY(calc(-100% + 1px));
  }
  @media screen and (max-width: 768px) {
    .scroll__content .scroll__text:before {
      display: none;
    }
  }
  
  .scroll__hero {
    position: sticky;
    z-index: -1;
  }
  
  .scroll__sectionwrapper {
    position: relative;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
  }
  
  .rectangles {
    position: relative;
    padding-top: 100%;
  }
  
  .rectangle {
    width: 4.6%;
    height: 19.8%;
    position: absolute;
    top: 50%;
    left: 50%;
    transition: background 1s cubic-bezier(0.41, 1.68, 0.55, 0.89);
    transform: translate(-50%, -50%);
    opacity: 1;
  }
  .no-skrollr .rectangle {
    transition: all 1s cubic-bezier(0.41, 1.68, 0.55, 0.89);
  }
  
